@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

table.results-table {
    font-family: 'Roboto', sans-serif;
    margin: auto;
    width: 100%;
    border-spacing: 0;
    text-align: center;
    font-size: 100%;
}

/* sticky table header */
table.results-table.sticky-head thead {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

/* table head cells */
table.results-table thead th {
    font-size:larger;
    font-weight: bold;
    margin-right: 0;
    border: none;
    color: white;
    background-color: rgb(4, 170, 109);
    padding-top: 10px;
    padding-bottom: 10px;
    border-spacing: 0px;
}

/* seconday header (below main header) */
table.results-table thead tr.secondary-head th {
    background-color: dimgray;
    font-size: inherit;
    padding: 1%;
    /*position: sticky;*/
    /*top: 2.7rem;*/
}

/* row as secondary header */
table.results-table tbody tr.secondary-head th {
    padding: 1%;
    background-color: dimgray;
}

/* all table cells */
table.results-table tr td {
    padding: 1%;
}

/*table.results-table tr td:first-child {
    font-weight: bold;
}*/

/* alternating row colour in body */
table.results-table tbody tr:nth-child(even) {
    background-color: lightgray;
    border: none;
}

table.results-table tbody tr:hover {
    background-color: lightblue;
}


.overlay {
    position: fixed; /* Sit on top of the page content */
    display: none; /* Hidden by default */
    width: 100%; /* Full width (cover the whole page) */
    height: 100%; /* Full height (cover the whole page) */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.8); /* Black background with opacity */
    z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
    overflow: hidden; /* overflow must be handled by child container */
  }

.link-button {
    border: none;
    background-color: rgba(0,0,0,0);
    width: -webkit-min-content;
    width: min-content;
    margin: 0;
    padding: 0;
}

.link-button-container > button {
    opacity: 0;
    transition: opacity .15s ease-in;
}
.link-button-container > button:hover {
    opacity: 1;
    transition: opacity .35s ease-out;
}
* {
  box-sizing: border-box;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.textarea-container {
  width: 100%;
  height: 50%;
}

#textarea {
  width: 50%;
  height: 25%;
  margin-top: calc(50%/8);
  display: inline-block;
  resize: none;
  font-size: medium;
  font-family: inherit;
}

.results {
  width: 100%;
  height: 50%;
  margin-top: 1%;
}

button {
  font-size: medium;
  background-color: lightseagreen;
  color: white;
  padding: 10px;
  border: none transparent;
  border-radius: 4px;
}

button[type=submit] {
  margin-top: 1%;
  cursor: pointer;
}

button[type=submit]:hover {
  background-color:cornflowerblue;
}

button[type=navigation] {
  background-color: black;
  border: 1px solid transparent;
  padding: 10px;
  font-size: 16px;
  margin-right: 1px;
}

button[type=navigation]:last-child {
  margin-right: 0px;
}

button[type=navigation]:hover {
  background-color: black;
  color: white;
  cursor: pointer;
}

button[type=navigation].active {
  background-color: gray;
}
button[type=navigation].active-all {
  background-color: pink;
}
